.App {
    text-align: left;
}

.title {
    color: #385BA9;
}

p,
h1,
h2 {
    color: #385BA9;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.btn-poke {
    background-color: #385BA9;
    color: white;
}

.btn-poke:hover {
    background-color: #17285E;
    color: white;
}

.logo-img {
    max-height: 200px;
}

.spinner {
    width: 100%;
    height: 100%
}